@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap");

* {
  box-sizing: border-box;
  text-decoration: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
body {
  height: 100%;
}

.container {
  max-width: 1200px;
  margin: 0px auto;
  padding: 20px 20px;
}

@media (max-width: 1024px) {
  .container {
    max-width: 900px;
  }
}

.slide__header {
  height: 100%;
  display: flex;
}

.slide__text {
  margin-top: 34px;
  margin-left: 50px;
  font-family: Roboto;
  border-left: 1px solid black;
  padding-left: 10px;
}
/* main */

.slide__main {
  position: absolute;
  top: 110px;
}
.slide__content__text {
  font-size: 50px;
  font-family: Roboto;
}
.slide__content__text_big {
  font-size: 90px;
  font-family: Roboto;
}
.main__botton_next {
  cursor: pointer;
  background: none;
  border: none;
  position: absolute;
  display: flex;
  margin: 0 auto;
  left: 50%;
  top: 52%;
}
.button_next {
  height: 68px;
  width: 270px;
}
/* mem */

.body__anim_a {
  position: absolute;
  left: 80%;
  bottom: 10%;
  animation: mem;
  animation-duration: 8s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}
.body__anim_b {
  animation: memb;
  animation-duration: 10s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  width: 150px;
  position: absolute;
  left: 50%;
  top: -10%;
}
.body__anim_c {
  width: 500px;
  position: absolute;
  left: -10%;
  top: 25%;
}
@keyframes mem {
  0% {
    transform: translateX(-3%);
  }
  20% {
    transform: translateX(0%);
  }
  40% {
    transform: translateX(60%);
  }
  60% {
    transform: translateY(80%);
  }

  100% {
    transform: translateX(-3%);
  }
}

@keyframes memb {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(-100%);
  }
  60% {
    transform: translateX(100%);
  }
  70% {
    transform: translateY(-150%);
  }
  80% {
    transform: translateY(100%);
  }
  100% {
    transform: translateX(0%);
  }
}
