html {
  background: url("/src/Components/bg.png");
}
#root {
  background: url("/src/Components/bg.png");
}
.container {
  width: 100%;
  margin: 0px auto;
  padding: 20px 20px;
}

@media (max-width: 1024px) {
  .container {
    max-width: 900px;
  }
}

.main__text {
  position: absolute;
  top: 140px;
  font-family: Roboto;
  font-size: 30px;
}
.main__text_scroll {
  font-family: Roboto;
  margin-top: 250px;
  margin-left: 50px;
  font-size: 30px;
  width: 750px;
  height: 450px;
  background: white;
  border-radius: 20px;
  padding: 10px 10px;
  border: 1px solid rgb(182, 182, 182);
  box-shadow: 1px 1px 3px 1px;
  overflow: scroll;
}
