* {
  box-sizing: border-box;
  font-family: Roboto;
  margin: 0 auto;
}

.container {
  width: 1024px;
  margin: 0px auto;
  padding: 20px 20px;
}

@media (max-width: 1024px) {
  .container {
    max-width: 900px;
  }
}
.main__img {
  position: absolute;
  width: 220px;
  bottom: 0;
}

.text_1 {
  font-size: 23px;
  padding: 20px 15px;
  position: absolute;
  border-radius: 40px;
  top: 250px;
  left: 250px;
  width: 320px;
  background: white;
}
.text_2 {
  font-size: 23px;
  padding: 20px 15px;
  position: absolute;
  border-radius: 40px;
  top: 250px;
  left: 600px;
  width: 200px;
  background: white;
}
.text_1__img {
  position: absolute;
  top: 200px;
  left: 260px;
  width: 70px;
}
.text_2__img {
  position: absolute;
  top: 210px;
  left: 610px;
  width: 80px;
}

.main__title {
  font-family: Roboto;

  top: 100px;
  position: relative;
  font-size: 40px;
  left: 250px;
}
.main__text {
  position: absolute;
  top: 150px;
}
.main__img_text {
  width: 400px;
  position: relative;
  top: 180px;
  left: 250px;
}
.main__buttom__img {
  position: absolute;
  text-decoration: none;
  border: none;
  background: none;
  width: 200px;
}
.main__buttom {
  text-decoration: none;
  border: none;
  background: none;
  position: absolute;
  bottom: 250px;
  margin-left: 200px;
}
