* {
  box-sizing: border-box;
}

.main__container {
  margin: 0 auto;
  height: 100%;
  max-width: 100%;
  display: flex;
}
.window {
  width: 100%;
  height: 100%;
  overflow: scroll;
}
.all-items__container {
  display: flex;
  height: 100%;
  transition: translate;
  transition-property: transform;
  transition-duration: 500ms;
  transition-timing-function: ease-in-out;
}
.button {
  position: fixed;
  margin: 0 auto;
  border: none;
  cursor: pointer;
  background: none;
  z-index: 10;
}

.slide__home {
  position: fixed;
  left: 90px;
  top: 46px;
  width: 30px;
}
.arrow {
  display: flex;

  position: relative;
  cursor: pointer;
}
