* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.popup {
  position: relative;
  margin-top: -130px;
  margin-left: -80px;
  background: rgba(0, 0, 0, 0.8);
  width: 1024px;
  height: 762px;
  border: 1px solid black;
  box-shadow: 1px 2px 3px 1px;
  display: flex;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  transform: scale(0);
}

.popup.active {
  transform: scale(1);
}

.popup__content {
  font-family: Roboto;
  padding: 20px;
  border-radius: 20px;
}
.popup__title {
  font-family: Roboto;
  position: absolute;
  top: 100px;
  left: 230px;
  background-color: white;
  border-radius: 20px;
  padding: 10px;
  font-style: bold;
  font-size: 50px;
  letter-spacing: 12px;
}
.popup__brand {
  font-family: Roboto;

  position: absolute;
  top: 200px;
  left: 230px;
  background-color: white;
  border-radius: 20px;
  padding: 10px;
  font-style: bold;
  font-size: 40px;
  letter-spacing: 13px;
}
.popup__main__li {
  font-family: Roboto;
  position: absolute;
  top: 300px;
  width: 700px;
  background-color: white;
  border-radius: 20px;
  padding: 10px;
  font-style: bold;
  font-size: 20px;
  overflow: scroll;
}
.popup__main__li {
  padding: 10;
  list-style: none;
  counter-reset: li;
}
.popup__main__li li {
  position: relative;
  padding: 12px 20px 20px 28px;
  margin-left: 40px;
  transition-duration: 0.3s;
}
.popup__main__li li:before {
  border: 6px solid transparent;
  line-height: 30px;
  position: absolute;
  top: 0;
  left: -30px;
  width: 42px;
  text-align: center;
  font-size: 13px;
  font-weight: bold;
  color: #2c97ef;
  counter-increment: li;
  content: counter(li);
  transition-duration: 0.3s;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.popup__main__li li:after {
  position: absolute;
  top: 0;
  left: -30px;
  width: 42px;
  height: 42px;
  border: 6px solid #ce80e5;
  border-radius: 50%;
  content: "";
  opacity: 0.5;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
